export default {
  name: 'fraction',
  props: ["mark","showNum"],
	data() {
		return {
      brightStart: [],
      darkStart: []
		}
	},
  created() {
    this.calcStart()
  },
	methods: {
    calcStart() {
      var me = this;
      var brightStartLen = parseInt(me.mark / 2);
      var darkStartLen = 5 - brightStartLen;
      for(var i=0; i<brightStartLen; i++){
        me.brightStart.push({id: 'bright' + i})
      }
      for( var j=0; j<darkStartLen; j++ ){
        me.darkStart.push({id: 'darkStart' + j})
      }
    }
	}
}
